import { useState } from 'react';
import states from "./data/states.json";
import CityFilter from "./CityFilter.js";
import CountyFilter from "./CountyFilter.js";
import Select from 'react-select'

function FilterForm( props ) {
  const [ selectedState, setSelectedState ] = useState( props.state );
  const [ selectedCounty, setSelectedCounty ] = useState( props.county );
  const [ selectedCity, setSelectedCity ] = useState( props.city );
  const [ tribalStatus, setTribalStatus ] = useState( '' );
  const [ selectedTribe, setSelectedTribe ] = useState( props.tribe );

  let grantees = [];
  if ( selectedState ) {
    grantees = props.data.filter(
      item => item["state-territory"] === selectedState 
    )
  }

  function updateFormState( selection ) {
    setSelectedState( selection.value );
    setSelectedCounty( '' );
    setSelectedCity( '' );
  }

  function handleTribeSelection( selection ) {
    setSelectedTribe( selection.value )
  }

  function handleTribeRadioSelection( event ) {
    setTribalStatus( event.target.value );
  }

  function updateAppState() {
    props.onSubmit(
      selectedState,
      selectedCounty,
      selectedCity,
      selectedTribe
    )
  }

  return (
    <div className="filters">
      <label htmlFor="state-select">What state or territory do you live in?</label>
      <Select options={ states }
              onChange={ updateFormState } />
      { selectedState &&
        <div>
          <p>There are { grantees.length } programs in your state. 
          Help us narrow down the ones that may be relevant to you.</p>
          <CountyFilter grantees={ grantees }
                        state={ selectedState }
                        county={ selectedCounty }
                        handler={ setSelectedCounty }/>
          <CityFilter grantees={ grantees }
                      state={ selectedState }
                      handler={ setSelectedCity }
                      city={ selectedCity }/>
          <div className="tribeFilters">
            <label className="block">Are you a Tribal member or living on Tribal lands?</label>
            <div>
            <input type="radio"
                   id="tribalAffiliationYes" 
                   name="tribalAffiliation" 
                   value="yes" 
                   onChange={ handleTribeRadioSelection }/>
            <label className="inline" htmlFor="tribalAffiliationYes">Yes</label>
            </div>
            <div>
            <input type="radio" 
                   id="tribalAffiliationNo" 
                   name="tribalAffiliation" 
                   value="no"
                   onChange={ handleTribeRadioSelection }/>
            <label className="inline"  htmlFor="tribalAffiliationNo">No</label>
            </div>
            { tribalStatus === 'yes' &&
              <div className="tribeSelect">
              <label>What is your Tribe or Tribally Designated Housing Entity (TDHE)?</label>
              <Select options={ props.tribeOptions }
                      onChange={ handleTribeSelection } />
              </div>
            }

          </div>
        </div>
      }
      <button onClick={ updateAppState }>Search</button>
    </div>
  );
}

export default FilterForm;
