import Select from 'react-select'

const CityFilter = ( props ) => {
  function handleChange( selection ) {
    props.handler( selection.value );
  }
  if (props.state) {
		const cities = props.grantees.filter(
			item => item.locality === 'City'
			).map(item => ({'label': item.name, 'value': item.name}) )
	  if ( cities.length ) {
	  	 return (
	  	 	<div>
			    <label htmlFor="city-select">Do you live in any of these cities?</label>
			    <Select options={ cities }
                  onChange={ handleChange }
                  key={ props.state }/>
		    </div>
		  );
	  }
	}
  return (null);
};

export default CityFilter;