import ResultsRow from "./ResultsRow.js";

const Results = ( props ) => {
  if (props.showResults) {
    let vals = props.data;
    if ( props.county || props.city ) {
      vals = vals.filter(
        item => (
          item.locality === 'State' && item['state-territory'] === props.state ||
          item.locality === 'County' && item.name === props.county ||
          item.locality === 'City' && item.name === props.city
        )
      )
    } else if ( props.state ) {
      vals = vals.filter(
        item => (
          item['state-territory'] === props.state
        )
      )
    }
    let tribeResult;
    if ( props.tribe ) {
      tribeResult = props.tribes.filter(
        item => (
          item.name === props.tribe
        )
      )
    }
    if (tribeResult) {
      vals = [...vals, ...tribeResult]
    }
    vals.sort(
      (a, b) => ( a.locality > b.locality ) ? 1 : -1
    ).reverse();
    if ( vals.length ) {
      return (
        <div className="results">
          <p>You may be eligible for { vals.length } programs.</p>
          <table>
            <thead>
              <tr>
                <th>Type</th>
                <th>Name</th>
                <th className="url-col">URL</th>
              </tr>
            </thead>
            <tbody>
              { vals.map( ( item, index ) => (
                <ResultsRow item={ item } key={ index }/>
              ) ) }
            </tbody>
          </table>
        </div>
      );
    }
    return (<div className="results">No results</div>)
  }
	return (null);
};

export default Results;