import Select from 'react-select';

const CountyFilter = ( props ) => {
	function handleChange( selection ) {
    props.handler( selection.value );
  }
	if (props.state) {
		const counties = props.grantees.filter(
			item => item.locality === 'County'
			).map(item => ({'label': item.name, 'value': item.name}))

		if ( counties.length ) {
	  	 return (
	  	 	<div>
		    	<label htmlFor="county-select">Do you live in any of these counties?</label>
					<Select options={ counties }
                  onChange={ handleChange }
                  key={ props.state } />
		    </div>
		  );
	  }
	}
  return (null);
};

export default CountyFilter;