import './App.css';
import { useState } from 'react';
import granteeStates from "./data/grantees-states.json";
import granteeTribes from "./data/grantees-tribes.json";
import granteeTerritories from "./data/grantees-territories.json";
import FilterForm from "./FilterForm.js";
import Results from "./Results.js";

const geographicData = [...granteeStates, ...granteeTerritories];
const tribeOptions = granteeTribes.map(
  item => ( { value: item.name, label: item.name } )
)

function App() {
  const [ state, setState ] = useState( '' );
  const [ county, setCounty ] = useState( '' );
  const [ city, setCity ] = useState( '' );
  const [ tribe, setTribe ] = useState( '' );
  const [ showResults, setShowResults ] = useState( false );

  function updateAppState(state, county, city, tribe) {
    setState( state );
    setCounty( county );
    setCity( city );
    setTribe( tribe );
    setShowResults( true );
  }

  return (
    <div className="App">
      <FilterForm data={ geographicData }
                  tribeOptions={ tribeOptions }
                  onSubmit={ updateAppState }
                  state={ state }
                  county={ county }
                  city={ city }/>
      <Results state={ state } 
               county={county} 
               city={city} 
               tribe={tribe}
               data={ geographicData }
               tribes={ granteeTribes }
               showResults={ showResults }/>
    </div>
  );
}

export default App;
